import Module from './modules/core/Module';

(function ( $ ) {

    $.fn.rotatingBanner = function ( conf ) {

        var defaults = {
                auto_play: true,
                current: 0,
                effect_speed: 0,
                change_speed: 4000,
                controls: null,
                control_type: 'indexed',
                play_control: null,
                pause_control: null,
                indicator: null,
                counter: null,
                effect_speed: 400
            },
            // user configurations override defaults
            conf = $.extend( defaults, conf );

        var banner_module = function () {
            return $.extend( {
                /* extends from: Module */
                elements: {
                    '.controls': 'controls',
                    '.controls [data-control="play"]': 'play_control',
                    '.controls [data-control="pause"]': 'pause_control',
                    '.controls [data-control="indicator"]': 'indicator',
                    '.controls [data-control="counter"]': 'counter',
                    '.banner': 'banner_list'
                },

                // events will be delegated to this.el
                events: {
                    'mouseenter': 'pause, toggle_controls',
                    'mouseleave': 'play, toggle_controls',
                    'click [data-control="play"]': 'play',
                    'click [data-control="pause"]': 'pause',
                    'click [data-control="next"]': 'next',
                    'click [data-control="previous"]': 'previous',
                    'click [data-control="indexed"] [data-index]': 'handle_index_change',
                    'indexchange:banner': 'update_indicator'
                },

                initialize: function ( element ) {
                    this.el = $( element );
                    if ( this.el[0].hasRotatingBanner ) {
                        return;
                    }

                    // prevent dupplicat modules on same DOM node
                    this.el[0].hasRotatingBanner = true;
                    this.current = conf.current;

                    this.update_elements();
                    if ( conf.control_type ) {
                        this.generate_controls( conf.control_type );
                    }

                    this.delegate_events();
                    if ( conf.auto_play ) {
                        this.play();
                    }
                    return this;
                },

                /**
                 * NOTE: this method assumes the controls will always be positioned at the bottom of the module
                 * and the css to do so is provided by the user
                 */
                toggle_controls: function ( e ) {
                    // if mouseenter show controls else hide using height of controls to determine position
//					this.controls.stop(true).animate( { bottom : (e.type == 'mouseenter') ? 0 : -(this.controls[0].offsetHeight) }, 150);
                    this.controls.toggleClass( 'on', 'mouseenter' === e.type );
                },

                play: function ( e ) {
                    // clear any existing timers to prevent unaccessble timers
                    this.clear_timer();
                    this.play_control.hide();
                    this.pause_control.show();

                    var self = this,
                        // if play button clicked start immediately
                        delay = (e && e.target === this.play_control[0] && e.type == 'click') ? 100 : conf.change_speed;

                    this.timer = setTimeout( function () {
                        self.next();
                        self.play();
                    }, delay );

                    return false;
                },

                pause: function ( e ) {
                    this.clear_timer();
                    this.play_control.show();
                    this.pause_control.hide();
                    return false;
                },

                previous: function () {
                    this.change_banner( this.index_range( this.current - 1 ) );
                    return false;
                },

                next: function () {
                    this.change_banner( this.index_range( this.current + 1 ) );
                    return false;
                },

                change_banner: function ( index ) {
                    this.banner_list.eq( this.current ).fadeOut( conf.effect_speed );
                    this.banner_list.eq( index ).fadeIn( conf.effect_speed );
                    this.el.trigger( {
                        type: 'indexchange:banner',
                        index: index
                    } );
                    this.current = index;
                },

                handle_index_change: function ( e ) {
                    var index = -1;
                    if ( e.target ) {
                        index = parseInt( e.target.getAttribute( 'data-index' ) );
                    }
                    this.change_banner( index );
                },

                update_indicator: function ( e ) {
                    // replace this method with appropriate function based on control type on initial call
                    var fn;
                    switch ( conf.control_type ) {
                        case 'indexed':
                            fn = function ( e ) {
                                this.controls.find( '[data-index]' ).removeClass( 'current' );
                                this.controls.find( '[data-index="' + e.index + '"]' ).addClass( 'current' );
                            };
                            break;
                        case 'player':
                            fn = function ( e ) {
                                this.counter.text( (e.index + 1) + ' / ' + (this.banner_list.length) );
                            };
                            break;
                        default:
                            fn = function () {
                            };
                    }
                    ;

                    fn.apply( this, arguments );
                    this.update_idicator = fn;
                },

                /*private methods*/
                clear_timer: function () {
                    if ( this.timer ) {
                        clearTimeout( this.timer );
                        this.timer = null;
                    }
                },

                index_range: function ( index ) {
                    var ban_count = this.banner_list.length;
                    // ensure index is within correct range
                    return (index > ban_count - 1) ? 0 : (index < 0) ? ban_count - 1 : index;
                },

                generate_controls: function ( control_type ) {

                    this.controls = $( '<div class="controls"></div>' );
                    var wrapper = $( '<div class="' +
                        control_type +
                        '-controls" data-control="' +
                        control_type +
                        '"></div>' );

                    switch ( control_type ) {
                        case 'indexed':
                            for ( var i = 0, l = this.banner_list.length; i < l; i++ ) {
                                wrapper.append( $( '<span data-index="' + i + '">' + i + '</span>' ) );
                            }
                            update.apply( this );
                            break;

                        case 'player':
                            var controls = ['previous', 'pause', 'play', 'next', 'counter'];
                            for ( var i = 0, l = controls.length; i < l; i++ ) {
                                wrapper.append( '<span data-control="' + controls[i] + '"></span>' );
                            }

                            update.apply( this );

                            break;

                        default: // do nothing
                    }

                    /* convenience method: append new controls to element and update module pointers */
                    function update() {
                        this.controls.append( wrapper );
                        this.el.append( this.controls );
                        this.update_elements();
                        this.update_indicator( {index: this.current} );
                    }

                }
            }, Module );

        };

        return $( this ).each( function () {
            return (new banner_module).initialize( this );
        } );
    };
})( jQuery );





